import "./post.css"

export default function Post() {
    return (
        <div className="post">
            <img className="postImg" src={process.env.PUBLIC_URL + "./dc.png"} alt=""></img>
            <div className="postInfo">
                <div className="postCategories">
                    <span className="postCategory">Music</span>
                    <span className="postCategory">Life</span>
                </div>
                <span className="postTitle">
                    The Post of Good
                </span>
                <hr />
                <span className="postDate">
                    1 hour ago
                </span>
            </div>
            <p className="postDesc">
                Harry Algericco, an actor at the theater, told The Huffington Post.  "She was really sweet, and that was a relief," said Algericco. "It was kind of scary. To see someone come down out of the theater, and say that. It was sad, but it felt like she had already lost hope, and she just had to be there."  Algericco recounted a scene she'd enjoyed at Shakespeare's Globe:  She was the only white person in the theater until the last scene when there was a group of black kids that were on stage with her.
            </p>
        </div>
    )
}
