import "./sidebar.css"

export default function SideBar() {
    return (
        <div className="sidebar">
            <div className="sidebarItem">
                <span className="sidebarTitle">ABOUT ME</span>
                <img src={process.env.PUBLIC_URL + "./my_pic.png"} alt=""></img>
                <p>
                    Packetsss is a machine learning enthusiast who believes in the power of data.  After graduating with a degree in computer science, Ryan had to choose an investment strategy. For a while, he was a hedge fund salesman, working the phones for well-established firms. But he grew dissatisfied with that kind of work, so he went back to school to study math. After about a year — which included his son's first birthday — Ryan decided to go back to work for an investment bank.  The move made him an overnight celebrity.
                </p>
            </div>
            <div className="sideBarItem">
                <span className="sidebarTitle">CATEGORIES</span>
                <ul className="sidebarList">
                    <li className="sidebarListItem">Life</li>
                    <li className="sidebarListItem">Music</li>
                    <li className="sidebarListItem">Style</li>
                    <li className="sidebarListItem">Sport</li>
                    <li className="sidebarListItem">Tech</li>
                    <li className="sidebarListItem">Cinema</li>
                </ul>
            </div>
            <div className="sideBarItem">
                <span className="sidebarTitle">FOLLOW US</span>
                <div className="sidebarSocial">
                    <i className="sidebarIcon fab fa-github-square"></i>
                    <i className="sidebarIcon fab fa-linkedin"></i>
                    <i className="sidebarIcon fab fa-facebook-square"></i>
                    <i className="sidebarIcon fab fa-twitter-square"></i>
                    <i className="sidebarIcon fab fa-instagram-square"></i>
                </div>
            </div>
        </div>
    )
}
